@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Lumi";
  src: local("Lumi"), url(/fromlumi/static/media/Lumi-Regular.3cc8ee00.otf) format("otf");
}
:root {
  --primary-o: #f0ae8e;
  --primary-g: #c7d9a7;
  --accent-b: #7dbbd2;
  --accent-y: #fff4e3;
  --accent-p: #facaca;
  --text: #083132;
  --background-w: #edeae5;
  --background-b: #f9f6e3;
}

body {
  background-color: #f7f6f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.link {
  font-family: "Dongle";
  font-size: 28px;
  color: #3a3a3a;
  font-weight: 400;
}
.link:hover {
  color: #f0ae8e;
  color: var(--primary-o);
}
#name {
  font-family: "Lumi";
  font-size: 72px;
  text-align: center;
  margin: 48px 0 24px 0;
  color: #3a3a3a;
}

#name:hover {
  color: #c7d9a7;
  color: var(--primary-g);
}
#navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
}

.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  text-decoration: none;
  width: 280px;
  padding: 16px;
  margin: 8px 0 8px 0;
  text-align: center;
  color: white;
  border-radius: 8px;
  font-family: "Lumi";
  font-size: 32px;
}

.button:hover {
  color: black;
}

.video-responsive {
  margin-bottom: 24px;
}

